@import url(http://fonts.cdnfonts.com/css/bahnschrift);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0}a{text-decoration:none}#loading{position:fixed;top:0;left:0;z-index:100;width:100vw;height:100vh;background-color:#8E6F5B;transition:0.5s;background-repeat:no-repeat;background-position:center;display:flex;justify-content:center;align-items:center;flex-direction:column}body{background:#8E6F5B;font-family:Bahnschrift;overflow-x:hidden}.welcome-container{position:relative;width:100vw;height:100vh;overflow:hidden}.welcome-container .background-img__dark{position:absolute;top:0;right:0}.welcome-container .background-img__light{position:absolute;top:40%;left:0}.welcome-container .background-img__cake{position:absolute;left:0;top:-10rem}.welcome-container .background-img__phone1{position:absolute;left:41%;top:1rem}.welcome-container .background-img__phone2{position:absolute;left:0;bottom:-10rem;transition:0.2s}.welcome-container .background-img__phone3{position:absolute;right:0;bottom:-15rem;transition:0.2s}.welcome-container .background-img__me{position:absolute;right:16rem;top:20rem;transition:0.2s;animation:animateMe 2s 1s linear infinite}.welcome-container__info{position:absolute;top:20rem;left:28rem}.welcome-container__info-title{display:flex;flex-direction:column;align-items:center;width:30rem;position:relative}.welcome-container__info-title__img{position:absolute;margin-bottom:0;top:-10rem}.welcome-container__info-title__name-block{width:100%;height:4rem;display:flex;align-items:center;justify-content:center;border-radius:10rem;position:relative;background:#27160B}.welcome-container__info-title__name-block p{color:#FFFFFF;font-family:Caveat;font-size:2rem}.welcome-container__info-title__name-block img{position:absolute;right:-1rem;width:4rem}.welcome-container__info ul{padding-left:1.5rem;padding-top:2rem;width:80%}.welcome-container__info ul li{color:#FFFFFF;font-family:Bahnschrift;font-size:2.5rem;list-style:none;margin-bottom:1.5rem;font-weight:500}.welcome-container__info ul li span{color:#C9AF92;margin-right:1rem}.map{width:100vw}.stat-block{position:relative;margin-top:4rem;width:100%;display:flex;align-items:center;flex-direction:column}.stat-block h2{font-family:Caveat;font-size:4rem;color:#FFFFFF;font-weight:normal}.stat-block__line{position:absolute;right:0}.stat-block__container{margin-top:2rem;margin-left:35rem;margin-bottom:5rem;height:calc(22rem - 4rem);width:calc(100vw - 20rem);padding:2rem 20rem;background:url(/bogomolchikk/static/media/stat-background.9ce6e90e.png);background-repeat:no-repeat;display:flex;flex-direction:row}.stat-block__container-item{display:flex;flex-direction:column;align-items:center;justify-content:center;width:20%;padding:0 1rem;text-align:center}.stat-block__container-item:nth-child(2){border-left:1px solid #E2E2E2;border-right:1px solid #E2E2E2}.stat-block__container-item p{font-family:Bahnschrift;font-style:normal;font-weight:600;font-size:1.5rem;margin-top:0.5rem;color:#585858}@keyframes animateMe{0%{transform:scale(1)}50%{transform:scale(1.05)}100%{transform:scale(1)}}.inst-block{display:flex;flex-direction:row;align-items:center;margin-top:3rem;margin-left:2rem;transition:0.2s}.inst-block p{color:#C6B2A6;margin-left:1rem;font-size:2.5rem;font-weight:bold;font-family:Bahnschrift}.inst-block:hover{transition:0.2s;transform:scale(1.1)}::-webkit-scrollbar{width:10px;box-shadow:inset 0px 4px 4px rgba(0,0,0,0.25)}::-webkit-scrollbar-track{border-radius:10px}::-webkit-scrollbar-thumb{background:#C6B2A6;border-radius:10px;box-shadow:4px 4px 1px rgba(0,0,0,0.1)}.slider{width:80%;margin:0 auto}.slider h2{font-family:Caveat;font-size:4rem;color:#FFFFFF;font-weight:normal;margin:0 auto;width:100%;text-align:center;margin-bottom:3rem}.rec-arrow-right{background:#8E6F5B !important}.rec-arrow-right:hover{background:#C9AF92 !important}.rec-arrow-left{background:#8E6F5B !important}.rec-arrow-left:hover{background:#C9AF92 !important}

@font-face {
    font-family: 'Caveat';
    src: local('Caveat Regular'), local('Caveat-Regular'), url(/bogomolchikk/static/media/Caveatregular.25b78766.woff2) format('woff2'), url(/bogomolchikk/static/media/Caveatregular.c565f84f.woff) format('woff'), url(/bogomolchikk/static/media/Caveatregular.8808f33c.ttf) format('truetype');
    font-weight: 400;
    font-style: normal;
}
@font-face {
    font-family: 'Caveat';
    src: local('Caveat Bold'), local('Caveat-Bold'), url(/bogomolchikk/static/media/Caveatbold.df9fb59e.woff2) format('woff2'), url(/bogomolchikk/static/media/Caveatbold.876042af.woff) format('woff'), url(/bogomolchikk/static/media/Caveatbold.bf18c80f.ttf) format('truetype');
    font-weight: 700;
    font-style: normal;
}

